  lineChartMethod() {
    this.lineChart = new Chart(this.lineCanvas.nativeElement, {
      type: 'line',

      data: {
        labels: this.dailybookedrevenue_label,

        datasets: [{ 
          label: "Daily",
            data: this.dailybookedrevenue_data,
            
            borderColor: "#3e95cd",
            backgroundColor:"transparent"
          }, 
           
        ]
      },
      options: {
        elements: {
          line: {
            tension : 0.4  // smooth lines
          },
        }}
    });

  }
